<template>
    <div 
      class="partner-card" 
      v-for="partner in partners"
      :key="partner.name"
    >
        <a href="#">
          <img :src="partner.image" alt="">
          <p class="company-name">{{partner.name}}</p>
        </a>
    </div>
</template>

<script>
export default {
  name: 'Partners-list',
  data() {
    return {
      partners: [
        {
          image: require('@/assets/images/partners/usiol.png'),
          name: 'Usiol',
          link: 'http://usiol.com'
        },
        {
          image: require('@/assets/images/partners/lacrivera.png'),
          name: 'Lacrivera',
          link: 'http://Lacrivera.com'
        },
      ]
    }
  },
};
</script>

<style lang="scss">
@mixin media-size($size){
  @media(max-width: $size){
    @content
  }
}

.partner-card{
  width: 100%;
  max-width: 25%;
  height: 150px;
  padding: 0 15px;
  text-align: center;
  margin-bottom: 30px;
  // Media
  @include media-size(1020px) {
    max-width: 30%;
  }
  @include media-size(790px) {
    max-width: 50%;
  }
  @include media-size(570px) {
    max-width: 100%;
  }
  
  a{
    position: relative;
    width: 100%;
    height: 100%;
    display: inline-block;
    
    img{
      position: relative;
      height: 70%;
    }
    
    p{
      width: 100%;
      padding: 10px;
      font-size: 18px;
      color: #555555;
      font-weight: 700;
    }
  }
}
</style>